import Button from "../UI/Button/Button";
import Card from "../UI/Card/Card";
import classes from "./Frogs.module.css"
const { useEffect, useState } = require("react");


const Frogs = () => {
	const [frogUrl, setFrogUrl] = useState("");
	const newFrog = async () => {
        console.log(images[Math.floor(Math.random()*images.length)]);
        setFrogUrl(images[Math.floor(Math.random()*images.length)]);
	}
	return (
		<Card>
			<img src={frogUrl} alt={frogUrl}></img>
			<Button className={classes.btn} onClick={newFrog}>New Frog</Button>
		</Card>
	);
};

