import { useState, useEffect } from "react";
import { DvdScreensaver } from "react-dvd-screensaver";

import Card from "../UI/Card/Card";
import Button from "../UI/Button/Button";
import classes from "./Game.module.css";

const Game = () => {
	const [speed, setSpeed] = useState(0);

	const [counter, setCounter] = useState(60);

	// Third Attempts
	useEffect(() => {
		const timer =
			counter > 0 && setInterval(() => setCounter(counter - 1), 1000);
		return () => clearInterval(timer);
	}, [counter]);

	const speedUp = () => {
		console.log(speed);
		if (counter != 0) setSpeed((speed) => speed + 1);
	};

	return (
		<>
			<Card className={classes.field}>
				<DvdScreensaver speed={0.3}>
					<img
						src="https://i.pinimg.com/originals/28/3e/53/283e53880ea4fd483c4968d89b143866.png"
						height={100}
						width={100}
						onClick={speedUp}
					/>
				</DvdScreensaver>
			</Card>
			<Card className={classes.field}>
				<DvdScreensaver speed={0.5}>
					<img
						src="https://freepngimg.com/thumb/categories/96.png"
						height={100}
						width={100}
						onClick={speedUp}
					/>
				</DvdScreensaver>
			</Card>
			<Card className={classes.field}>
				<DvdScreensaver speed={0.4}>
					<img
						src="https://down.imgspng.com/download/2113/frog_PNG3838.png"
						height={100}
						width={100}
						onClick={speedUp}
					/>
				</DvdScreensaver>
			</Card>
			<Card className={classes.card}>
				<div className={classes.result}>
					TOTAL BONDI POINTS: {speed}
					<div>COUNTDOWN: {counter}</div>
				</div>
			</Card>
		</>
	);
};

export default Game;
